















































import { Vue, Component, Prop, Watch, Emit } from 'vue-property-decorator';
interface Itemtask  {
  id: number;
  value: string;
  tasked: boolean;
}
type isplice = (key: number, len: number) => void;
interface Itasks {
  [index: number]: Itemtask;
  splice: isplice;
}
@Component
export default class Dialog extends Vue {
  public dialog: boolean = false;
  public newVal: string = '';
  @Prop({
    type: Number,
    default: 0,
    required: true,
  })
  public appDialog!: number;
  @Prop({
    type: Number,
    default: 0,
    required: true,
  })
  public keys!: number;
  @Prop({
    type: Array,
    default: [
      {
        id: 0,
        value: '',
        tasked: false,
      },
    ],
    required: true,
  })
  public tasks!: Itasks;
  @Watch('appDialog')
  public onAppDialog(val: number, oldVal: number) {
    this.newVal = this.tasks[this.keys].value;
    this.dialog = !this.dialog;
  }
  @Emit('saveTasks')
  public saveTasks() {
    return true;
  }
  private async saveVal() {
    this.tasks[this.keys].value = this.newVal;
    this.dialog = false;
    try {
      const tasks = await this.$store.dispatch('asyncChangeTasks', this.tasks);
      console.log('保存成功');
      this.saveTasks();
    } catch (Error) {
      console.error(Error);
    }
  }
  private async deleteVal() {
    try {
      const key: number = await this.$store.dispatch('asyncDelTasks', this.keys);
      this.dialog = false;
    } catch (Error) {
      console.error(Error);
    }
  }
}
